import { CError } from "@components";
import { Component } from "react";

class CServerError extends Component {
  constructor(props) {
    super(props);
    this.state = { hasError: false };
  }
  static getDerivedStateFromError(error) {
    return { hasError: true };
  }

  render() {
    if (this.state.hasError) {
      return (
        <CError
          handleBack={() => this.setState({ hasError: false })}
          status={500}
        />
      );
    }
    return this.props.children;
  }
}

export default CServerError;
